@import url(https://fonts.googleapis.com/css?family=Montserrat:300,500,700);
.listItem__wrapper {
    list-style: none;
    padding: 30px 0;
    margin: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.listItem__image {
    -webkit-flex-shrink:0;
            flex-shrink:0;
    margin-right: 30px;
    width: 150px;
    height: 150px;
    border-radius: 50%;
}

.listItem__name {
    margin: 0;
    color: #1E58FF;
    font-weight: 700;
}

.listItem__description {
    margin: 10px 0 20px;
    font-weight: 300;
}

.listItem__button {
    padding: 7px 12px;
    font-weight: 500;
    background: none;
    border: 2px solid #1E58FF;
    color: #1E58FF;
}
.listWrapper__wrapper {
    width: 80vw;
    margin: 100px auto 0;
}
*, *::before, *::after {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
}
